.item-container {
    height: 50px;
    width: 50px;
    background-color: white;
    margin: 1px;
    color: rgb(75, 75, 75);
    font-weight: bold;
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.item-input {
    text-align: center;
    color: black;
    font-size: 2rem;
    font-weight: bold;
    background: transparent;
    border: 0;
    width: 100%;
    height: 100%;
    outline: none;
}

.show-answer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    color: green;
    height: 100%;
    width: 100%;
    background-color: green;
    animation: showAnswer 300ms ease forwards;

}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

@keyframes showAnswer {
    from{
        transform: rotateY(180deg);
    }
    to{
        transform: rotateY(0deg);
        color: white;
    }
    
}